
import { defineComponent, ref } from "vue";
export default defineComponent({
  props: {
    title: {
      type: String,
      default: "hello",
    },
  },
  setup(props, { emit }) {
    const activate = ref(false);
    function clickHandler() {
      activate.value = !activate.value;
      emit("cc", activate.value);
    }
    return { activate, clickHandler };
  },
});
