<template>
  <div class="featureClick">
    <div class="container" ref="container">
      <div class="content">
        <div class="box">
          <div class="label">姓名:</div>
          <div class="info">{{ name }}</div>
        </div>
        <div class="box">
          <div class="label" style="width:100%">介绍:</div>
          <div class="info des">
            {{ info.des }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script lang="ts">
import store from "@/store/index";
import Map from "ol/Map";
import { popupInit, popupShow } from "@/oljs/popup";
import { defineComponent, onMounted, ref, watch } from "vue";
import Overlay from "node_modules/@types/ol/Overlay";
import { FeatureLike } from "ol/Feature";
import { initFeatureClick } from "@/oljs/event";

export default defineComponent({
  setup() {
    let map: null | Map = null;
    let popup: null | Overlay = null;
    const container = ref<HTMLElement>();

    const name = ref("");
    const info = ref({});

    watch(
      () => store.state.clickFeature,
      (f) => {
        const custom = f?.get("custom");
        popupShow(popup as Overlay, custom.coordinate);
        name.value = custom.name;
        info.value = custom.info;
      }
    );

    onMounted(() => {
      map = store.state.map;
      initFeatureClick(map as Map);
      popup = popupInit(map as Map, container.value as HTMLElement);
    });

    return { container, name, info };
  },
});
</script>
<style lang="scss" scoped>
.container {
  position: absolute;
  transform: translate(-50%, calc(-100% - 2rem));
  .content {
    font-size: 0.8rem;
    // ----------------
    width: 10rem;
    height: 8rem;
    padding: 0 1rem;
    background: #fff;
    border-radius: 1rem;
    white-space: nowrap;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    box-shadow: 0 0 3px #00d2d3, 0 3px 6px #00d2d3;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      bottom: -3px;
      left: 50%;
      transform: translateX(-50%) rotateZ(45deg);
      width: 10px;
      height: 10px;
      background: #fff;
    }
    .box {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      .des {
        white-space: pre-wrap;
        width: 100%;
        height: 5rem;
        overflow-y: auto;
      }
    }
  }
}
</style>
